
.shop{
    
    width: 90vw;
    height: 100%;
    margin: auto;
    
}
.shoptitle{
    display: grid;
    justify-content: center;
    margin-top: 10vh;
    
    
}
.ShopText{
    font-size: 3.5em;
    font-family: 'Bebas Neue', cursive;
    color: rgb(111, 73, 66);
    font-weight: 700;
}
.Products{
    margin-top: 0px;
    width: 90vw;
    height: auto;
    margin: auto;
    display: grid;
    justify-content: space-evenly;
    grid-template-columns: auto auto auto;
    place-items: center;
    
    
}
.product{
    margin-top: 10px;
    padding: 50px;
}
.img{
    height: 250px;
    overflow: hidden;
}
.Products img{
    width: 200px;
    
}
.Products .product{
    text-align: center;
}
.product:hover{
    transition: 0.3s ease-in;
    
}
.add{
    padding: 15px;
    width: 20vw;
    
    background-color: rgb(111, 73, 66);
    border-radius: 10px;
    color: white;
}
.informaçoes{
    margin-top: 5vh;
    margin-bottom: 2vh;
    display: flex;
    justify-content: space-around;
}
@media only screen and (max-width: 1250px) {
    .Products{
       
        display: grid;
        grid-template-columns: 1fr 1fr;

    }
  }
  @media only screen and (max-width: 850px) {
    .Products{
       
        display: grid;
        grid-template-columns: 1fr;

    }
  }
  @media only screen and (max-width: 470px) {
    .Products{
       
        display: grid;
        grid-template-columns: 1fr;

    }
    .Products{
        margin-top: 50px;
        width: 100vw;
    }
    .ShopText{
        font-size: 2em;
    }
    .Products img{
        width: 250px;
        margin-bottom: 50px;
    }
  }
  @media only screen and (max-width: 360px) {
    
    .Products{
        margin-top: 30px;
        width: 100vw;
    }
    .ShopText{
        font-size: 2em;
        padding: 5px;
        width: 100vw;
        text-align: center;

    }
    .Products img{
        width: 200px;
        margin-bottom: 50px;
    }

  }