
@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Dancing+Script&family=League+Spartan:wght@100&family=Open+Sans:wght@300&display=swap');

.cart{
    background-image: url('../../public/bg.png');

    width: 100vw;
    height: auto;
    min-height: 100vh;
    display: grid;
    justify-content: center;
}
.containerCart{
    padding-top: 100px;
    width: 90vw;
    margin: auto;
    height: auto;
    display: grid;
    justify-content: center;
    text-align: center;
}
.cartTitle{
    
    padding: 25px;
    font-weight: 800;
    font-family: 'Bebas Neue', cursive;
    font-size: 3em;
    color: rgb(111, 73, 66);
}
.CartItems{
    margin: auto;
    max-width: 1000px;
    width: 80vw;
    height: 20vh;
    margin-bottom: 40px;
    
    display: flex;
    justify-content: space-between;
    border-radius: 10px;
    align-items: center;
    background-color: rgb(47, 42, 42);
}
.detalhes{
    max-width: 25vw;
    color: white;
}
.contImg{
    margin-left: 10px;
    width: 30vw;
   
}
.CartItems img{
    width: auto;
    height: 17vh;
}
.CartItems p{
    margin-right: 30px;
    font-size: 1.4em;
    text-align: center;
    
}
.contador p button{
    background-color: #ffffff00;
    width: 35px;
    height: 40px;
}

.contador p input{
    width: 7vw;
    text-align: center;
    border: 1px solid white;
    color: white;
    background-color: #ffffff00;
}
.checkout{
    text-align: center;
    margin: 10px;
    width: 70vw;
    
}
.checkout button{
    background-color: rgb(111, 73, 66);

    color: white;
    padding: 20px;
    margin-left: 45px;
    border-radius: 10px;
    margin-top: 25px;
}
.checkout p {
    color: white;
}
.erro{
    display: flex;
    justify-content: center;
    align-items: start;
    margin-top: 80px;
    padding: 25px;
    font-weight: 800;
    font-family: 'Bebas Neue', cursive;
    font-size: 3em;
    color: rgb(111, 73, 66);
}
@media only screen and (max-width: 570px) {
    .CartItems{
        max-width: 1000px;
        width: 100vw;
        height: 30vh;
        margin-bottom: 40px;
        box-shadow: 1px 2px 12px #ffffff00;
        display: flex;
        justify-content: space-around;
        border-radius: 10px;
        align-items: center;
    }
    .detalhes{
        display: grid;
        justify-content: center;
        width: 40vw;
    }
    .CartItems p{
        margin-right: 0px;
        font-size: 1.4em;
    }
    .contador p button{
    
        width: 20px;
        height: 40px;
    }
    
    .contador p input{
        width: 7vw;
        text-align: center;
        border: 1px solid white;
    }
  }
@media only screen and (max-width: 400px) {
    .CartItems{
        
        height: 35vh;
        
    }
    .CartItems img{
        width: auto;
        height: 10vh;
    }
    .cartTitle {
        padding: 25px;
        font-weight: 800;
        font-size: 1.5em;
        text-align: center;
    }
}
@media only screen and (max-height: 500px) {
    .CartItems {
        
        margin-bottom: 60px;
        
    }
    .CartItems{
        
       
        box-shadow: 1px 2px 12px #ffffff00;
        
    }
}
@media (min-width: 800px) and (max-height: 800px) {
    .CartItems {
        
        height: 30vh;
        
    }
}
@media (min-width: 800px) and (max-height: 550px) {
    .CartItems {
        
        height: 35vh;
        
    }
}
@media (min-width: 250px) and (max-height: 550px) {
    .CartItems {
        
        height: 45vh;
        
    }
}