@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Dancing+Script&family=League+Spartan:wght@100&family=Open+Sans:wght@300&display=swap');

.dd{
    overflow: hidden;
}
.containe{
    padding-top: 45px;
    width: 100vw;
   
    height: auto;
    min-height: 100vh;
    display: grid;
    align-items: center;
    grid-template-columns: 30vw auto;
    background-image: url('../../public/bg.png');
    justify-content: space-evenly;
    
}

.boxtext h2{
    font-family: 'Bebas Neue', cursive;
    font-size: 4.5em;
    font-weight: 800;
    margin-bottom: 4vh;
    color: rgb(111, 73, 66);
}
.boxtext p{
    font-size: 0.8em;
    color: white;
}
.boxtext button{
    padding: 10px;
    width: 15vw;
    margin-top: 4vh;
    background-color: rgb(111, 73, 66);
    border-radius: 10px;
    color: white;
}
div img{
    width: 500px;
}


/* section About */

.sectionAbout{
    
    width: 90vw;
    margin: auto;
    height: 80vh;
    display: grid;
    grid-template-columns:  50% auto;
    justify-content: center;
    align-items: center;
}
.sectionAboutConteinerImg{
    width: 500px;
    border-radius: 20px;
    overflow: hidden;
    
}
.sectionAboutBoxtext h2{
    font-size: 3em;
    font-family: 'Bebas Neue', cursive;
    color: rgb(111, 73, 66);
    font-weight: 900;

}
.sectionAboutBoxtext p{
    font-size: 0.9em;
}
.sectionAboutBoxtext button{
    padding: 10px;
    width: 15vw;
    margin-top: 4vh;
    background-color: rgb(111, 73, 66);
    border-radius: 10px;
    color: white;
}


/* Section Customers */


.sectionCustomers{
    overflow: hidden;
    width: 90vw;
    margin: auto;
    height: auto;
    display: grid;
    padding-bottom: 7vh;
    text-align: center;
}
.sectionCustomers .sectionCustomersTitle{
    font-family: 'Bebas Neue', cursive;
    font-size: 3em;
    font-weight: 800;
    margin-bottom: 4vh;
    color: rgb(111, 73, 66);
    margin-top: 10vh;
}
.sectionCustomersConatiner{
    display: grid;
    grid-template-columns: 25% 25% 25%;
    justify-content: space-around;
    align-items: center;
}
.sectionCustomersBox{
    width: 330px;
    height: 350px;
    margin-top: 4vh;
    
    padding: 10px;
    border-radius: 10px;
    display: grid;
    align-items: center;
    
}
.sectionCustomersBoxImg{
    margin: auto;
    width: 90px;
    height: 90px;
    border-radius: 100%;
    overflow: hidden;
    margin-top: 4vh;
}
.sectionCustomersBox p {
    font-size: 1em;
    margin-top: 2vh;
}
.sectionCustomersBoxImg img{
    width: 90px;
}


@media only screen and (max-width: 1270px) {
    div img{
        width: 600px;
    }
    .sectionAbout{
    
        width: 100vw;
    }
    .sectionAboutBoxtext{
        width: 400px;
        text-align: end;
    }
    .sectionCustomersConatiner {
        display: grid;
        grid-template-columns: auto auto;
        justify-content: space-around;
        align-items: center;
    }
    .sectionCustomers {
        width: 90vw;
        margin: auto;
        height: auto;
        display: grid;
        text-align: center;
    }

}
@media only screen and (max-width: 1000px) {
    div img{
        
        width: 400px;
    }
    .container{
        display: grid;
        grid-template-columns:auto;
        grid-template-rows: auto auto;
        width: 40vw;
        margin: auto;
    }
    .conteinerImg{
        margin: auto;
    }
    .boxtext button{
        padding: 10px;
        width: 30vw;
        margin-bottom: 10px;
        
    }
    .sectionAbout{
        
        display: grid;
        grid-template-rows: auto auto;
        text-align: center;
    }


}
@media (max-width: 900px) {
    .sectionAboutConteinerImg{
        
        display: none;
    }
    .sectionAbout{
        height: auto;
        margin-top: 5vh;
        margin-bottom: 5vh;
    }
    .sectionAboutBoxtext {
        width: 500px;
        text-align: start;
    }
    .boxtext{
        width: 60vw;
        margin-top: 10vh;
    }
}
@media (max-width: 780px) {
    .containe{
        grid-template-columns:  auto;
    }
    .sectionCustomersConatiner {
        display: grid;
        grid-template-columns: auto;
        justify-content: center;
        align-items: center;
    }
}
@media (max-width: 690px){
    .sectionAbout{
        justify-content: flex-start;
        padding: 25px;
    }
    .sectionAboutBoxtext button{
        padding: 10px;
        width: 30vw;
        margin-top: 4vh;
        background-color: rgb(111, 73, 66);
        border-radius: 10px;
        color: white;
    }
    .add{
        width: 30vw;
    }
}
@media (max-width: 540px) {
    
    .boxtext{
        width: 90vw;
        margin: auto;
        
        
    }
    
    .boxtext h2{
        margin-top: 80px;
    }
    .sectionAboutBoxtext p {
        font-size: 0.9em;
    }
    .sectionAboutBoxtext {
        width: 400px;
        text-align: start;
    }

}
@media (max-width: 454px) {
    .sectionAboutBoxtext {
        width: 300px;
        text-align: start;
    }
    .boxtext{
        width: 90vw;
        margin: auto;
        
        
    }
    .sectionAboutBoxtext button{
        width: 40vw;
    }
    .add{
        width: 40vw;
    }

    .boxtext h2{
        margin-top: 80px;
        font-size: 2;
    }
    div img {
        width: 350px;
    }
    .sectionCustomersBox {
        
        box-shadow: 1px 2px 12px #ffffff;
        
    }
    .sectionCustomersBox p {
        font-size: 1em;
        margin-top: 2vh;
    }
}
@media (max-width: 364px) {
    .sectionCustomersBox p {
        font-size: 0.8em;
        margin-top: 2vh;
    }
    .sectionCustomersBox {
        width: 250px;
        height: auto;
        margin-top: 4vh;
        box-shadow: 1px 2px 12px #a9a5a500;
        padding: 10px;
        
    } .sectionAbout {
        width: 100vw;
    }
    .sectionAboutBoxtext {
        width: 250px;
        text-align: start;
    }
    .add {
        width: 50vw;
    }
    .boxtext button {
        width: 40vw;
    }
    
}

