#navbar {
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;
    position: fixed;
}
.ativaCor{
    background-color: black;
    
}

.link{
    padding: 4vh;
    text-decoration: none;
    color: white;
    margin-right: 0px;
   
}

.links img{
    width: 80px;
    margin-left: 4vw;
}
@media (max-width: 840px) {
    .dds{
        display: none;
    }
    .logo{
        font-size: 1.5em;
        margin-left:2vw;
    }
    .link{
        padding: 0vh;
        margin-right: 2vw;
    }

}

@media (max-width: 480px) {
    
    .logo{
        font-size: 1.5em;
        margin-left:2vw;
    }
    .link{
        padding: 0vh;
        margin-right: 2vw;
    }

}